.feedback-form {
  max-width: 480px;
  background-color: rgba(247, 218, 242, .882);
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 8px;
  flex-direction: column;
  margin-top: 20px;
  margin-left: 20px;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #7bb6fa;
  flex-direction: column;
  margin-bottom: 16px;
  font-weight: 700;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #7bb6fa;
  background-color: #f7daf2;
  border: 2px solid rgba(143, 194, 252, .718);
  border-radius: 6px;
  padding: 8px 12px;
  font-weight: 700;
}

.feedback-form button:hover, .feedback-form button:focus {
  color: #3d83d3;
  border: 2px solid #549ff5;
}

input, textarea {
  font: inherit;
  background-color: #f7daf2;
  border: 2px solid rgba(143, 194, 252, .718);
  border-radius: 6px;
  padding: 8px;
}

input:hover, textarea:hover, input:focus, textarea:focus {
  border: 2px solid #549ff5;
  outline: rgba(0, 0, 0, 0);
}

textarea {
  resize: none;
}

